.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.footer {
  padding: 1rem;
  background-color: #33b5e5;
  border: 2px solid #fff;
  color: #fff;
  height: 100px;
  text-align: center;
  }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.text-align-right{
  font-weight: bold;

  color: white;
}

.App-link {
  color: #f0f5f7;
}

.nav-link{
  text-decoration-color: #f5fafa;
  color: #D3D3D3;
  
}
.menu-active
{
  border-bottom: 1px solid rgb(19, 140, 211) ;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.profile-img{
  height: 850px;
  background-image: url("Deepesh.jpg");
  
  background-position: 0 15%;
 

  /* Center and scale the image nicely */
  background-repeat: no-repeat;
  background-size: cover;
 
}

.intro{
  padding: 320px 0;
 
}

.Profile-skills{
  font-weight: bold;
  color: #D3D3D3;
}